export default {
	name: 'AddNewPhone',
	data() {
		return {
			title: '',
			concat: '',
			phone: ''
		}
	},
	methods: {
		addNewPhone () {
			if (!this.title) {
				this.$toast('请输入服务标题')
				return
			}
			if (!this.concat) {
				this.$toast('请输入联系人')
				return
			}
			if (!this.phone) {
				this.$toast('请输入联系电话')
				return
			}
			if (!this.$utils.validator.isPhone(this.phone)) {
				this.$toast('请输入合法的联系方式')
				return
			}
			this.$http({
				url: this.$urlPath.addPhoneInfo,
				params: {
					cid: this.$routeParams.cid,
					title: this.title,
					concat: this.concat,
					phone: this.phone
				},
				onRequestSuccess: (res) => {
					uni.showModal({
						content: '添加成功，请耐心等待后台审核…',
						success: (res) => {
							this.$back()
						}
					})
				}
			})
		}
	}
}
